import React, { useEffect } from "react";
import { Typography } from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import styles from "./Home.module.css";
import { fetchEmailLoginToken } from "../../../actions/actions";
import { Helmet } from "react-helmet-async";

const Home = () => {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetchEmailLoginToken());
  }, [dispatch]);

  const store = useSelector((state) => state.reducers);

  const quote =
    '"Perfection (in design) is achieved not when' +
    " there is nothing more to add, but rather when" +
    ' there is nothing more to take away."';

  const mainText =
    "Custom software development for the small to medium sized business." +
    " Have a project in mind? ";

  const subText = "[ frontend, backend, machine learning, mobile, ecommerce ]";

  return (
    <div className={styles.home}>
      <Helmet>
        <title>4Front Software Development LLC</title>
        <meta
          name="description"
          content={
            "Custom software development for the small to medium sized business." +
            "[ frontend, backend, machine learning, mobile, ecommerce ]"
          }
        />
      </Helmet>
      {store.header}
      <Typography className={styles.quote} variant="subtitle1">
        {quote}
      </Typography>
      <section className={styles.container}>
        <Typography className={styles.mainText} variant="h5">
          {mainText}
          <Link className={styles.contactlink} to="/contact">
            Let&apos;s chat!!
          </Link>
        </Typography>
        <Typography className={styles.subText} variant="body1">
          {subText}
        </Typography>
      </section>
      {store.footer}
    </div>
  );
};

export default Home;
