import React, { useState } from "react";
import { Link } from "react-router-dom";
import { Drawer, List, ListItem, Typography } from "@material-ui/core";
import styles from "./Menu.module.css";
import Hamburger from "../../img/hamburger.svg";

const Menu = ({ navigation }) => {
  const [menuActive, setMenuActive] = useState(false);

  const toggleMenu = (bool) => (e) => {
    if (e.type === "keydown" && (e.key === "Tab" || e.key === "Shift")) {
      return;
    }
    setMenuActive(bool);
  };

  return (
    <div>
      <div onClick={toggleMenu(true)} role="presentation">
        <img className={styles.hamburgericon} src={Hamburger} alt="hamburger" />
      </div>
      <Drawer anchor="right" open={menuActive} onClose={toggleMenu(false)}>
        <nav
          role="presentation"
          onClick={toggleMenu(false)}
          onKeyDown={toggleMenu(false)}
          className={styles.menu}
        >
          <List className={styles.menulist}>
            {navigation.map((val, i) => (
              <ListItem
                style={{ textAlign: "center" }}
                button
                key={i.toString()}
              >
                <Link to={val.route} className={styles.link}>
                  <Typography variant="h5">{val.text}</Typography>
                </Link>
              </ListItem>
            ))}
          </List>
        </nav>
      </Drawer>
    </div>
  );
};

export default Menu;
