import React, { useState, useEffect } from "react";
import Recaptcha from "react-recaptcha";
import { useSelector } from "react-redux";
import { Typography, TextField, Button, Grid } from "@material-ui/core";
import styles from "./Contact.module.css";
import Api from "../../../utils/Api";
import { Helmet } from "react-helmet-async";

const Contact = () => {
  const store = useSelector((state) => state.reducers);

  const [inputs, setInputs] = useState({
    fullName: "",
    email: "",
    message: "",
  });

  const [formErrors, setFormErrors] = useState({
    fullName: false,
    email: false,
    message: false,
    recaptcha: false,
  });

  const [refreshRecaptcha, setRefreshRecaptcha] = useState(false);
  const [verifyRecaptcha, setVerifyRecaptcha] = useState(false);
  const [emailSuccess, setEmailSuccess] = useState(false);

  useEffect(() => {
    /**
     *  react-recaptcha library has bug  where component loads faster than
     *  its network requests on page refresh. I can't Async/Await a third party
     *  library's network requests so setTimout below delays the mounting of
     *  the component on refresh.
     */
    setTimeout(() => {
      setRefreshRecaptcha(true);
    }, 2000);
  }, []);

  const handleRecaptchaSecurity = (response) => {
    if (response) {
      setVerifyRecaptcha(true);
    }
  };

  const handleFormErrors = (isEmailValid, bool) => {
    const tempObj = {};
    Object.keys(inputs).forEach((key) => {
      if (bool === false) tempObj[key] = bool;
      if (bool === true && !inputs[key]) tempObj[key] = bool;
      if (isEmailValid === !bool) tempObj.email = bool;
      if (verifyRecaptcha === !bool) tempObj.recaptcha = bool;
    });
    setFormErrors({ ...formErrors, ...tempObj });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const emailRegex = new RegExp("@");
    const isEmailValid = emailRegex.test(inputs.email);
    handleFormErrors(isEmailValid, true);

    if (
      verifyRecaptcha &&
      inputs.fullName &&
      inputs.email &&
      isEmailValid &&
      inputs.message
    ) {
      try {
        await Api.sendContactEmail(inputs, store.emailLoginToken);
        setInputs("");
        setEmailSuccess(true);
        handleFormErrors(isEmailValid, false);
      } catch (err) {
        console.error(err);
      }
    }
  };

  const handleInputs = (e) => {
    setInputs({
      ...inputs,
      [e.target.name]: e.target.value,
    });
  };

  const formInputConfig = [
    {
      error: formErrors.fullName,
      name: "fullName",
      userInput: inputs.fullName,
      label: "Full Name",
      type: "text",
      isTextArea: false,
      textAreaSize: null,
    },
    {
      error: formErrors.email,
      name: "email",
      userInput: inputs.email,
      label: "Email Address",
      type: "email",
      isTextArea: false,
      textAreaSize: null,
    },
    {
      error: formErrors.message,
      name: "message",
      userInput: inputs.message,
      label: "Message",
      type: "text",
      isTextArea: true,
      textAreaSize: 5,
    },
  ];

  return (
    <div className={styles.contact}>
      <Helmet>
        <title>Contact Us</title>
        <meta name="description" content="Contact Us" />
      </Helmet>
      {store.header}
      <Typography color="primary" variant="h4" style={{ margin: "1rem" }}>
        Contact Us
      </Typography>
      {formErrors.recaptcha && (
        <Typography variant="h6" color="secondary">
          Please verify recaptcha
        </Typography>
      )}
      {emailSuccess && (
        <Typography variant="h6" color="primary">
          Email sent successfully
        </Typography>
      )}
      <form
        noValidate
        // autoComplete="off"
        onSubmit={handleSubmit}
        className={styles.form}
      >
        <Grid container direction="column">
          {formInputConfig.map((val, i) => (
            <TextField
              style={{ margin: "1rem" }}
              key={i.toString()}
              error={val.error}
              helperText={val.error && "invalid"}
              name={val.name}
              value={val.userInput || ""}
              onChange={handleInputs}
              id={val.name}
              label={val.label}
              required
              type={val.type}
              multiline={val.isTextArea}
              rows={val.textAreaSize}
            />
          ))}
          <Button
            type="submit"
            className={styles.button}
            size="medium"
            variant="contained"
            color="primary"
          >
            Send
          </Button>
        </Grid>
      </form>
      {refreshRecaptcha && (
        <Recaptcha
          sitekey={process.env.REACT_APP_GOOGLE_RECAPTCHA_KEY}
          render="explicit"
          onloadCallback={() => console.log("ReCaptcha Loaded")}
          verifyCallback={handleRecaptchaSecurity}
        />
      )}
      <div>{store.footer}</div>
    </div>
  );
};

export default Contact;
