import React from "react";
import { useSelector } from "react-redux";
import { Typography, Grid, Paper, Link } from "@material-ui/core";
import styles from "./Workshop.module.css";
import WorkshopData from "../../../utils/WorkshopData";
import { Helmet } from "react-helmet-async";

const Workshop = () => {
  const store = useSelector((state) => state.reducers);
  return (
    <div className={styles.workshop}>
      <Helmet>
        <title>Workshop</title>
        <meta name="description" content="Workshop" />
      </Helmet>
      {store.header}
      <Typography align="center" variant="h4" className={styles.heading}>
        Workshop
      </Typography>
      <section className={styles.container}>
        {WorkshopData.map((entry, i) => (
          <Paper className={styles.paper} key={i.toString()}>
            <Link
              href={entry.url}
              color="inherit"
              target="_blank"
              rel="noopener"
            >
              <Grid container style={{ padding: "1rem" }} direction="column">
                <Grid item>
                  <Typography variant="h5">{entry.title}</Typography>
                </Grid>
                <Grid item>
                  <Typography style={{ fontSize: ".8rem", padding: "1rem" }}>
                    {entry.subtitle}
                  </Typography>
                </Grid>
              </Grid>
            </Link>
          </Paper>
        ))}
      </section>
      {store.footer}
    </div>
  );
};

export default Workshop;
